<template lang="pug">
    form.feedback-form(
    @submit.prevent="submitForm"
    action="https://formspree.io/front-end-developer@protonmail.com"
    )
        .feedback-form__container
            .feedback-form__title Форма обратной связи
            input.feedback-form__input-text(
            type="text"
            placeholder="Имя"
            name="name"
            :class="{ 'feedback-form__input-text_error': $v.name.$error }"
            v-model.trim="$v.name.$model"
            )
            input.feedback-form__input-text(
            type="text"
            placeholder="Фамилия"
            name="surname"
            :class="{ 'feedback-form__input-text_error': $v.surname.$error }"
            v-model.trim="$v.surname.$model"
            )
            input.feedback-form__input-text(
            type="email"
            placeholder="Почта"
            name="email"
            :class="{ 'feedback-form__input-text_error': $v.email.$error }"
            v-model.trim="$v.email.$model"
            )
            the-mask.feedback-form__input-text(
            placeholder="Номер телефона"
            name="tel"
            :class="{ 'feedback-form__input-text_error': $v.tel.$error }"
            v-model.trim="$v.tel.$model"
            mask="+# (###) ###-##-##"
            )
            textarea.feedback-form__textarea(
            placeholder="Поле ввода текста"
            name="descr"
            :class="{ 'feedback-form__textarea_error': $v.descr.$error }"
            v-model.trim="$v.descr.$model"
            )
            input.feedback-form__btn(
            type="submit"
            :disabled="submitStatus === 'PENDING'"
            value="Отправить"
            )
            p.feedback-form__text-error(v-if="submitStatus === 'OK'") Спасибо! Ваша заявка отправлена.
            p.feedback-form__text-error(v-if="submitStatus === 'ERROR'") Пожалуйста, заполните форму правильно.
            p.feedback-form__text-error(v-if="submitStatus === 'PENDING'") Отправка формы. Пожалуйста подождите.
</template>

<script>
	import {required} from 'vuelidate/lib/validators';
	import $ from 'jquery';

	export default {
		name: 'FeedbackForm',
		data() {
			return {
				name: '',
				surname: '',
				email: '',
				tel: '',
				descr: '',
				submitStatus: null
			}
		},
		validations: {
			name: {
				required
			},
			surname: {
				required
			},
			email: {
				required
			},
			tel: {
				required
			},
			descr: {
				required
			}
		},
		methods: {
			submitForm() {
				console.log('submit!');
				this.$v.$touch();
				if (this.$v.$invalid) {
					this.submitStatus = 'ERROR';
				} else {
					// do your submit logic here
					this.submitStatus = 'PENDING';
					$.ajax({
						url: "https://formspree.io/front-end-developer@protonmail.com",
						method: "POST",
						data: {message: "hello!"},
						dataType: "json"
					});
					// $.ajax({
					// 	url: '//formspree.io/front-end-developer@protonmail.com',
					// 	method: 'POST',
					// 	data: $(this).serialize(),
					// 	dataType: 'json',
					// 	beforeSend: function () {
					// 		$('.feedback-form').append('<div class="alert alert--loading">Sending message</div>');
					// 	},
					// 	success: function (data) {
					// 		$('.feedback-form').find('.alert--loading').hide();
					// 		$('.feedback-form').append('<div class="alert alert--success">Message sent!</div>');
					// 	},
					// 	error: function (err) {
					// 		$('.feedback-form').find('.alert--loading').hide();
					// 		$('.feedback-form').append('<div class="alert alert--error">Ops, there was an error.</div>');
					// 	}
					// });
					setTimeout(() => {
						this.submitStatus = 'OK';
					}, 500);
				}
			},
			sendEmail() {
				$.ajax({
					url: "https://formspree.io/front-end-developer@protonmail.com",
					method: "POST",
					data: {message: "hello!"},
					dataType: "json"
				});
			}
		}
	}
</script>

<style lang="sass" scoped>
    @import "../assets/_common/styles/app"
    .feedback-form
        &__container
            @include glass(400px)
            height: 100vh
            display: flex
            justify-content: flex-start
            align-items: center
            flex-direction: column
            margin-top: 150px
        &__title
            text-align: center
            font-family: $font-pt-sans
            font-weight: bold
            font-size: 26px
            list-style: 1.2
            color: $color-black
            margin: 0 0 40px
        &__input-text
            @include default-text-input
            margin: 0 0 30px
        &__textarea
            @include default-textarea
            margin: 0 0 40px
        &__btn
            @include default-btn
            max-width: 200px
            margin: 0 0 15px
        &__text-error
            text-align: center
            font-size: 14px
            font-family: $font-pt-sans
</style>



