import Vue from 'vue';
import Vuelidate from 'vuelidate';
import VueTheMask from 'vue-the-mask';
import Router from 'vue-router';
import FeedbackForm from '@/components/FeedbackForm';

Vue.use(Router);
Vue.use(Vuelidate);
Vue.use(VueTheMask);

export default new Router({
	routes: [
		{
			path: '/',
			name: 'FeedbackForm',
			component: FeedbackForm
		}
	]
})



